<script>
export default {
  data() {
    return {
      isLoggedIn: false,
      loading: false,
      userInfo: {},
      hasUserInfo: false
    }
  },
  onLoad() {
    this.checkLoginStatus()
  },
  methods: {
    async checkLoginStatus() {
      try {
        const token = uni.getStorageSync('token')
        if (token) {
          this.isLoggedIn = true
          this.userInfo = uni.getStorageSync('userInfo') || {}
          this.hasUserInfo = !!this.userInfo.username
        }
      } catch (error) {
        console.error('Ê£ÄÊü•ÁôªÂΩïÁä∂ÊÄÅÂ§±Ë¥•:', error)
      }
    },
    async silentLogin() {
      if (this.loading) return
      this.loading = true
      try {
        // Âø´ÈÄüÁôªÂΩïÈÄªËæë
        await uni.login({
          provider: 'weixin',
          success: async (loginRes) => {
            if (loginRes.code) {
              // Ë∞ÉÁî®ÂêéÁ´ØÁôªÂΩïÊé•Âè£
              // TODO: ÂÆûÁé∞ÁôªÂΩïÈÄªËæë
            }
