          fail: (err) => {
            uni.showToast({
              title: 'ÁôªÂΩïÂ§±Ë¥•',
              icon: 'none'
            })
            console.error('ÁôªÂΩïÂ§±Ë¥•:', err)
          }
        })
      } catch (error) {
        console.error('ÁôªÂΩïÂ§±Ë¥•:', error)
        uni.showToast({
          title: 'ÁôªÂΩïÂ§±Ë¥•ÔºåËØ∑ÈáçËØï',
          icon: 'none'
        })
      } finally {
